import React from 'react';
import styled from 'styled-components';
import { Section, Container } from '@components/global';
import EmailForm from '@ui/emailform';
//import Call2Act from '@ui/Call2Act';
import { StaticQuery, graphql } from 'gatsby';
//import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';

import gifStickers from '@images/photos/stickersani.gif';

const Channelling = () => (
  <StaticQuery
    query={graphql`
      query {
        Link2Act: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "link2act" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="subscribe" accent="primary">
        <Container>
          <HeaderWrapper accent="inverse">
          <Grid>
            <div>
            <Fade>
              <h2>Stay Tuned For Secret Perk</h2>
              <h4>We need your help to complete Hai! <span role="img" aria-label="smil emoji">😊</span></h4>
              <p>Please sign up to our newsletter and receive a secret perk only for our subscribers when we launch our Indiegogo campaign. You will also get an exclusive article <i>How do hard of hearing people listen to music</i>.<br/></p>
              <EmailForm/>
              {/*
                <Call2Act text="Sign up to our newsletter" href="https://www.indiegogo.com" accent="inverse" target="_blank"/>
              */}
            </Fade>
            </div>
            <Fade>
              <LinkMainImg>
                <img src={gifStickers} alt="Stickers"/>
              </LinkMainImg>
            </Fade>
          </Grid>
          </HeaderWrapper>
        </Container>
      </Section>
    )}
  />
);

export default Channelling;


const HeaderWrapper = styled.div`
  padding: 0;
  margin-left: 0%;
  max-width: 100%;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0 0 0;
    margin-left: 0%;
    margin-bottom: -50px;
  }

  ${props =>
    props.accent &&
    `color: ${
      props.accent === 'inverse'
        ? props.theme.color.white.regular
        : props.theme.color.grayscale.S1000
    }`};

  p {
    ${props =>
      props.accent &&
      `color: ${
        props.accent === 'inverse'
          ? props.theme.color.grayscale.S100
          : props.theme.color.grayscale.S1000
      }`};
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 4fr 3fr;
  grid-gap: 50px;
  text-align: left;
  align-items: center;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    margin-bottom: 96px;
    grid-gap: 25px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const LinkMainImg = styled.div`
margin: 0;
max-width: 1380px;
min-width: 30vw;
padding: auto;

transform: rotate(10deg);
img{
  margin: 0;
  width: 100%;
}
@media (max-width: ${props => props.theme.screen.sm}) {
  min-width: calc(80vw - 32px);
}
`



